import React, { useContext, useState } from "react";
import Modal from "../UI/Modal";
import classes from "./Cart.module.css"
import CartContext from "../../store/cart-context";
import CartItem from "./CartItem";
import Checkout from "./Checkout";


const Cart = props =>{
    const cartCtx=useContext(CartContext);
    const [isCheckout,setIsCheckout]=useState(false);
    const [isSubmitting,setIsSubmitting]=useState(false);
    const [didSubmit ,setDidSubmit]=useState(false);
    const totalAmount = `${cartCtx.totalAmount.toFixed(2)} rupees`;
    const hasItems=cartCtx.items.length > 0 ;

   
      const cartItemRemoveHandler = (id) => {
          cartCtx.removeItem(id)
      };

      const cartItemAddHandler = (item) => {
          cartCtx.addItem({...item,amount:1});
      };
      const orderHandler=()=>{
         setIsCheckout(true);
      }
      const submitOrderHandler = async (userData)=>{
          setIsSubmitting(true);
           const response = await fetch('https://react-http-1c50f-default-rtdb.firebaseio.com/orders.json',{
              method: 'POST',
              body:JSON.stringify({
                  user:userData,
                  orderedItems:cartCtx.items
              })
          });
          setIsSubmitting(false);
          setDidSubmit(true);
          cartCtx.clearCart();
      }

    const cartItems =<ul className={classes['cart-items']}>{cartCtx.items.map((item)=><CartItem key={item.id} name={item.name} amount={item.amount}
    price={item.price} onRemove= {cartItemRemoveHandler.bind(null,item.id)} onAdd={cartItemAddHandler.bind(null,item)}/>)
    }</ul>
    const modalActions=(
        <div className={classes.actions}>
        <button onClick={props.onClose} className={classes['button--alt']}>Close</button>
        {hasItems && <button className={classes.button} onClick={orderHandler} >Order</button>}
    </div>
    )
    const cartModalContent =(
        <React.Fragment>
            {cartItems}
            <div className={classes.total}>
                <span>Total Amount</span>
                <span>{totalAmount}</span>
            </div>
            {isCheckout && <Checkout onConfirm={submitOrderHandler} onCancel ={props.onClose} />}
             {!isCheckout && modalActions} 
        </React.Fragment>
    )
     const isSubmittingModalContent = <p>Sending order data...</p>;
     const didSubmitModalContent =(<React.Fragment>
          <p>Order Confirmed !</p>
           <div className={classes.actions}>
        <button onClick={props.onClose} className={classes.button}>Close</button>
       </div>

     </React.Fragment>);
    return(
        <Modal onClose={props.onClose}>
            {!isSubmitting && !didSubmit && cartModalContent}
            {isSubmitting && isSubmittingModalContent}
            {!isSubmitting && didSubmit && didSubmitModalContent}
                    
        </Modal>
    )
}

export default Cart;